.image {
    height: 300px;
    width: 300px;
    img {
        object-fit: cover;
        height: 300px;
        width: 300px;
        border-radius: 25px;
        box-shadow: 0px 5px 10px rgba(0,0,0,0.2);
    }
}

.randomrow {
    margin-top: 25px;
}

.random {
    max-width: 1200px;
    width: 100%;
}


.album-thumbnail {
    height: 100px;
    width: 100px;
    img {
        height: 100px;
        width: 100px;
        border-radius: 10px;
    }
}



.row {
    width: 100%;
    .artist-header {
        align-items: center;
        h1 {
            margin: 0px 10px 0 0;
        }
    }
}

.artistinfo {
    margin-left: 20px;
}

.info {
    margin-top: 50px;
    h2 {
        margin-top: 50px;
    }
}



.related.m-0 {
    margin: 0px;
}


.moreinfo {
    display: flex;
    flex-direction: column;
}

.tags {
    margin-top: 10px;
    display: flex;
    flex-wrap: wrap;
    small {
    background: rgb(255, 255, 255);
    margin: 0 5px 5px 0;
    padding: 5px 10px;
    border-radius: 10px;
    }
}